import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import AppNav from "./components/AppNav";
import HomePage from "./components/HomePage/index";
import LocationPage from "./components/LocationPage/index";
import NearbyPage from "./components/NearbyPage/index";
import { BrowserRouter as Router, Link, Route } from "react-router-dom";

import * as serviceWorker from "./serviceWorker";

const AppRouter = () => {
  return (
    <Router>
      <AppNav />
      <Route path="/" exact component={HomePage} />
      <Route path="/place" component={LocationPage} />
      <Route path="/nearby" component={NearbyPage} />
    </Router>
  );
};

ReactDOM.render(<AppRouter />, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA

if ("serviceWorker" in navigator) {
  navigator.serviceWorker.register("/service-worker.js").catch(function(err) {
    console.info("Service workers are not supported.");
  });
}
serviceWorker.register();
